import {EventEmitter, Inject, Injectable} from '@angular/core';
import {ColorSet, DefaultAssets, KonversoInterface, KonversoQuery, KonversoUser} from '../interface/KonversoInterface';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {BehaviorSubject} from 'rxjs';
import * as mustache from 'mustache';

@Injectable()
export class KonversoService {
  public authentication: EventEmitter<KonversoUser> = new EventEmitter<KonversoUser>();
  public assets: DefaultAssets;
  public firstVisit = false;
  public firstUsageStory: string[];
  public ColorSet: ColorSet;
  public PlaceHolder: string[];
  public AssistantMode = false;
  public Welcome: string;
  private locale: string;
  private token: BehaviorSubject<string> = new BehaviorSubject<string>(null);
  // tslint:disable-next-line:variable-name
  private _token = this.token.asObservable();
  private user: KonversoUser;
  private header: HttpHeaders;
  private endpoint: string;

  constructor(@Inject('__NgxKonverso__') config: KonversoInterface, private http: HttpClient) {
    this.buildHeaders();
    this.initInstance(config);
  }

  /**
   * Send Query To backend server and get a response
   * @param query
   */
  public async send(query: string): Promise<string | any> {
    return new Promise((resolve, reject) => {
      if (query && query.replace(new RegExp(' ', 'g'), '')) {
        if (this.header) {
          const preparedData = this.buildQuery(query);
          const options: any = {
            headers: this.header
          };
          this.http.post(this.endpoint, preparedData, options)
            .subscribe((data: any) => {
              resolve(data);
            });
        } else {
          reject(new Error('Header is not set, please provide a token though user setting or though module configuration'));
        }
      } else {
        reject(new Error('Current input is empty, please try again'));
      }
    });
  }

  /**
   * @private
   * Generate Header for backend call
   */
  private buildHeaders() {
    this._token.subscribe((token: string) => {
      const bearer: string = 'Bearer ' + token;
      this.header = new HttpHeaders({
        //'Content-Type': 'application/json',
       // 'Access-Control-Allow-Origin': window.location.origin,
        'Authorization': bearer
      });
    });
  }

  /**
   * @param config
   * @private
   * Initialize Data for User Instance
   */
  private initInstance(config: KonversoInterface) {
    if (config.endpoint) {
      this.endpoint = config.endpoint;
      if (config.AssistantMode) {
        this.AssistantMode = config.AssistantMode;
      }


      if (config.defaultAssets) {
        this.assets = config.defaultAssets;
        if (config.defaultAssets.ColorSet) {
          this.ColorSet = config.defaultAssets.ColorSet;
        }
      }
      if (config.auth) {
        this.authentication.subscribe((user: KonversoUser) => {
          if (!user.lang && config.lang) {
            user.lang = config.lang;
          }
          this.locale = user.lang;
          if (config.InputPlaceHolder && config.InputPlaceHolder[this.locale]) {
            this.PlaceHolder = config.InputPlaceHolder[this.locale];
          }
          if (config.CustomWelcome && config.BotInitMessage.Welcome && config.BotInitMessage.Welcome[this.locale]) {
            this.Welcome = mustache.render(config.BotInitMessage.Welcome[this.locale], user);

          }
          if (user.token) {
            this.token.next(user.token);
          }
          if (user.firstVisit) {
            this.firstVisit = true;
            delete user.firstVisit;
            if (config?.BotInitMessage?.FirstUsage &&
              this.locale &&
              config?.BotInitMessage?.FirstUsage[this.locale]) {
              this.firstUsageStory = [];
              for (const history of config.BotInitMessage.FirstUsage[this.locale]) {
                this.firstUsageStory.push(mustache.render(history, user));
              }
            }
          }
          this.user = user;
        });
      } else if (config.lang) {
        this.locale = config.lang;
        if (config.InputPlaceHolder && config.InputPlaceHolder[this.locale]) {
          this.PlaceHolder = config.InputPlaceHolder[this.locale];
        }
        if (config.CustomWelcome && config.BotInitMessage.Welcome && config.BotInitMessage.Welcome[this.locale]) {
          this.Welcome = config.BotInitMessage.Welcome[this.locale];
        }
        this.user = {
          userId: this.guid(),
          lang: config.lang
        };
      }
      if (config.token) {
        this.token.next(config.token);
      }
    } else {
      return new Error('Please provide endpoint');
    }
  }

  /**
   * @param query
   * @private
   * prepare set data to push to backend server
   */
  private buildQuery(query: string): KonversoQuery {
    // @ts-ignore
    return {
      ...this.user,
      query: query.replace(/\s+/g, ' ').trim(),
      isSending: true
    };
  }

  /**
   * @private
   * Generate Random uniq Id for Konverso Instance
   */
  private guid() {
    let random = () => {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    };
    return random() + random() + '-' + random() + '-' + random() + '-' + random() + '-' + random() + random() + random();
  }
}
