import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {DefaultAssets} from '../../interface/KonversoInterface';

@Component({
  selector: 'bot-first-visit',
  templateUrl: './first-visit.component.html',
  styleUrls: ['./first-visit.component.scss']
})
export class FirstVisitComponent implements OnInit {
  @Input() firstUsageStory: string[];
  @Input() assets: DefaultAssets;
  @Output() ready: EventEmitter<boolean> = new EventEmitter<boolean>(false);
  public position = 0;
  public current = null;

  constructor() {
  }

  ngOnInit(): void {
    this.current = this.firstUsageStory[this.position];
    const clear = setInterval(() => {
      if (this.position < (this.firstUsageStory.length - 1)) {
        this.current = this.firstUsageStory[++this.position];
      } else {
        clearInterval(clear);
      }
    }, 5000);
  }

  goTo(pos: number) {
    this.position = pos;
    this.current = this.firstUsageStory[this.position];
  }

  start(): void {
    this.ready.emit(true);
  }

}
