import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {KonversoService} from './konverso.service';
import {ColorSet, DefaultAssets, KonversoAnswer, OpenChatBotResponse, UserInput} from '../interface/KonversoInterface';
import {BotMessageSample} from '../Sample/BotMessageSample';

// @ts-ignore
@Component({
  selector: 'ngx-konverso',
  templateUrl: 'konverso.component.html',
  styleUrls: ['../../assets/main.scss']
})
export class KonversoComponent implements OnInit {
  @Output() ready: EventEmitter<boolean> = new EventEmitter<boolean>();
  public query: string;
  public isMobile: boolean;
  public assets: DefaultAssets;
  public firstVisit: boolean;
  public firstUsageStory: string[];
  public AssistantMode = false;
  public colorSet: ColorSet;
  public History: (UserInput | OpenChatBotResponse)[];
  public disableUserInput = false;
  public LastUserInput: UserInput;
  public LastBotAnswer: OpenChatBotResponse;
  public PlaceHolder: string[];
  public Welcome: string;

  constructor(private service: KonversoService) {
  }

  ngOnInit(): void {
    this.isMobile = this._isMobile();
    this.assets = this.service.assets;
    this.firstVisit = this.service.firstVisit;
    this.firstUsageStory = this.service.firstUsageStory;
    this.AssistantMode = this.service.AssistantMode;
    this.PlaceHolder = this.service.PlaceHolder;
    this.Welcome = this.service.Welcome;

    this.History = [];
    if (this.service.ColorSet) {
      this.colorSet = this.service.ColorSet;
    }
    this.ready.subscribe((ready: boolean) => {
      if (ready) {
        this.firstVisit = false;
        this.service.firstVisit = false;
      }
    });
    if (this.Welcome) {
      const customWelcome: OpenChatBotResponse = BotMessageSample;
      customWelcome.text = this.Welcome;
      this.LastBotAnswer = customWelcome;
      this.History.push(customWelcome);
    }
    this.sendBotCommand('exit', false).catch((err: any) => console.log('fail reset session'));
  }

  async send($event: UserInput) {
    if ($event.message === 'exit') {
      this.sendBotCommand('exit', false).catch((err: any) => console.log('fail reset session'));
      return false;
    }
    this.History.push($event);
    if (this.AssistantMode) {
      if (this.LastUserInput) {
        this.LastUserInput.message += ' ' + $event.message;
        this.LastUserInput.date = $event.date;
      } else {
        this.LastUserInput = $event;
      }
    } else {
      this.LastUserInput = $event;
    }

    const index: number = this.History.length - 1;
    const response: KonversoAnswer = await this.service.send($event.message).catch((err: string) => {
      console.log('We got an error ', err);
      // @ts-ignore
      this.History[index].error = true;
    });
    if (response && response.response) {
      if (response.response.medias && response.response.medias[0] && response.response.medias[0].required_actions &&
        response.response.medias[0].required_actions.length) {
        this.disableUserInput = true;
      } else {
        this.disableUserInput = false;
      }
      this.LastBotAnswer = response.response;
      this.History.push(response.response);
    }
  }

  async sendBotCommand($event: string, push: boolean = true) {
    const response: KonversoAnswer = await this.service.send($event).catch((err: string) => {
      console.log('We got an error ', err);
    });
    if (response.response.medias && response.response.medias[0] && response.response.medias[0].required_actions &&
      response.response.medias[0].required_actions.length) {
      this.disableUserInput = true;
    } else {
      this.disableUserInput = false;
    }
    if (response && response.response && push) {
      this.LastBotAnswer = response.response;
      this.History.push(response.response);
    } else if (response && response.response && !push){
      this.LastUserInput = null;
      this.LastBotAnswer = response.response;
      this.History.push(response.response);
    } else {
      this.LastUserInput = null;
    }
  }

  private _isMobile(): boolean {
    const isMobile = {
      Android: (): boolean => {
        return !!navigator.userAgent.match(/Android/i);
      },
      BlackBerry: (): boolean => {
        return !!navigator.userAgent.match(/BlackBerry/i);
      },
      iOS: (): boolean => {
        return !!navigator.userAgent.match(/iPhone|iPad|iPod/i);
      },
      Opera: (): boolean => {
        return !!navigator.userAgent.match(/Opera Mini/i);
      },
      Windows: (): boolean => {
        return !!navigator.userAgent.match(/IEMobile/i) || !!navigator.userAgent.match(/WPDesktop/i);
      },
      any: (): boolean => {
        return !!(isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows());
      }
    };
    return isMobile.any();
  }


}
